.header-fixed {
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: cornsilk;
    animation-name: show_header;
    animation-duration: 1s;
}

@keyframes show_header {
    0%{
        opacity: 0;
        top: -70px;
    }
    100%{
        opacity: 1;
        top: 0;
    }
}